ol 
{
    padding: 0%
} 

ol li{
    border: 3px solid whitesmoke;
   background-color: bisque;
   margin: 3px;
   padding: 5%;
   list-style: none;
   width: 80%;
   font-family: 'Glyphicons Halflings';
}

ol li p
{
