}



.hide-redo{
    display: none;
}
.hide-done{
    display: none;
}
.taskButton{
    float: right;
    margin: 10%
}



.board{
    margin: 3%;
    padding: 5%
}

.panel button 
{
    border-radius: 3px;
    color: #fff;
    cursor: default;
    font-size: 12px;
    /*height: 32px;*/
    line-height: 32px;
    margin: 0 2px 4px 2px;
    overflow: hidden;
    position: relative;
    text-decoration: none;
